import React, { Component } from "react";

import {
  Icon,
  Form,
  Input,
  Button,
  List,
  Modal,
  notification,
  Avatar
} from "antd";

const {TextArea} = Input
const confirm = Modal.confirm;
const key = 'updatable';

export default class MessageListItem extends Component {
  state = {
    collapsed: true,
    visible : false
  };

  openNotification = (message,desc) => {
    notification.open({
      key,
      message: message,
      description: desc,
    });
  };

  showDeleteConfirm = () => {
    const {deleteMessage,id} = this.props
    confirm({
      title: "–í—ã —Ç–æ—á–Ω–æ —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –¥–∞–Ω–Ω–æ–µ —Å–æ–æ–±—â–µ–Ω–∏–µ ?",
      content: "–ù–∞–∂–∏–º–∞—è –∫–Ω–æ–ø–∫—É –î–ê –≤—ã –ø–æ–¥—Ç–≤–µ—Ä–∂–¥–∞–µ—Ç–µ —Å–≤–æ–µ –¥–µ–π—Å—Ç–≤–∏–µ",
      okText: "–î–∞",
      okType: "danger",
      cancelText: "–ù–µ—Ç",
      onOk() {  
        
        const options = {
          method: "DELETE"
        };
        const url = `/message/${id}`;
    
        fetch(url, options)
          .then(() => deleteMessage(id));
      },
      onCancel() {
        console.log("–û—Ç–º–µ–Ω–∞");
      }
    });
  }

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    // console.log(e);
    // console.log(this.props);

    const {id,editMessage} = this.props

    this.openNotification("–°–æ–æ–±—â–µ–Ω–∏–µ","–ò–∑–º–µ–Ω–µ–Ω–Ω–æ")
    // const { id } = this.props
    const { tag,text } = this.state

    this.setState({
      visible: false,
    });

    const data = {
      tag,
      text
    }
    
    const options = {
      method: "PUT",
      body: JSON.stringify(data),
      headers: {
        'Content-Type' : "application/json"
      }
    };
    const url = `/message/${id}`;

    fetch(url, options)
      .then(() => editMessage(id,tag,text)
      );
  };

  handleCancel = e => {
    this.setState({
      visible: false
    });
  };

  onChangeHandler = e => {
    const { name,value } = e.target
    this.setState({ [name] : value })
  }

  render() {
    const {tag,text,author,avatar} = this.props

    return (
      <div>
        <List.Item
          actions={[
            <Button onClick={this.showModal}>–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å</Button>,
            <Button onClick={this.showDeleteConfirm}>–£–¥–∞–ª–∏—Ç—å</Button>
          ]}>
          <List.Item.Meta 
            avatar={<Avatar src={avatar} />}
            title={
             <div>
                <a href="/">{tag}</a> <br/>
                <a href="/">{author}</a>
             </div> 
            } 
            description={text} />
        </List.Item>
        <Modal
            title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ —Å–æ–æ–±—â–µ–Ω–∏—è"
            cancelText={"–û—Ç–º–µ–Ω–∞"}
            okText={"–ò–∑–º–µ–Ω–∏—Ç—å"}
            visible={this.state.visible}
            onOk={this.handleOk}
            onCancel={this.handleCancel}
          >
            <Form>
              <Form.Item>
                <Input
                  value={this.state.tag}
                  name="tag"
                  onChange={this.onChangeHandler}
                  prefix={
                    <Icon type="tag" style={{ color: "rgba(0,0,0,.25)" }} />
                  }
                  placeholder="–ù–∞–ø–∏—à–∏—Ç–µ —Å–≤–æ–π tag"
                />
              </Form.Item>
              <Form.Item>
                <TextArea 
                  value={this.state.text}
                  name="text"
                  onChange={this.onChangeHandler}
                  rows={4} 
                  placeholder="–ù–∞–ø–∏—à–∏—Ç–µ —Ç–µ–∫—Å—Ç —Å–≤–æ–µ–≥–æ —Å–æ–æ–±—â–µ–Ω–∏—è" />
              </Form.Item>
            </Form>
          </Modal>
      </div>
    );
  }
}
