import React, { Component } from "react";

import MessageCreate from '../contaners/messageCreate'
import MessageList from '../contaners/messageList'

import {
  Tabs
} from "antd";

const TabPane = Tabs.TabPane;

export default class Panel extends Component {
  onChangeHandler = tab => {
    const {setTab} = this.props
    setTab(tab)
  }

  render() {
    const { activeTab } = this.props.messageReducer
    
    return (
      <Tabs activeKey={activeTab ? activeTab : "1"} onChange={this.onChangeHandler}>
        <TabPane tab="–°–ø–∏—Å–æ–∫ —Å–æ–æ–±—â–µ–Ω–∏–π" key="1">
          <MessageList/>
        </TabPane>
        <TabPane tab="–°–æ–∑–¥–∞–Ω–∏–µ —Å–æ–æ–±—â–µ–Ω–∏—è" key="2">
          <MessageCreate />
        </TabPane>
      </Tabs>
    );
  }
}
